.add-listing-dialog {
  overflow-y: scroll;
}

.add-listing-dialog-container {
  z-index: 100;
  height: fit-content;
  margin-top: 5%;
  display: flex;
  flex-direction: column;
  gap: 1em;
  width: 100%;
  justify-content: center;
  padding: 0 5%;
  max-width: 950px;
}
.add-listing-ul-container {
  display: flex;
  gap: 1em;
}
.add-listing-ul {
  display: flex;
  flex-direction: column;
  gap: 1em;
  width: 100%;
}

.add-listing-ul-2 {
  width: 100%;
}

.add-listing-image {
  width: 150px;

  height: 150px;
  margin-top: 1em;
  border-radius: var(--b-rad-btn);
  object-fit: cover;
}

.create-listing-btn {
  margin-top: 1em;
  display: block;
  width: 100%;
}

.add-listing-btn-container {
  display: flex;
  width: 100%;
  gap: 1em;
}

@media screen and (max-width: 850px) {
  .add-listing-ul-container {
    flex-direction: column;
  }
  .add-listing-dialog-container {
    max-width: 600px;
  }
}
