.register-form {
  display: flex;
  flex-direction: column;
  gap: 2em;

  width: fit-content;
  margin: 0 auto;
}

.register-form-ul {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  flex-wrap: wrap;
  column-gap: 3em;
  row-gap: 1em;
}

.register-btn {
  background-color: var(--blue-2);
}

.register-btn:hover {
  background-color: var(--blue-1);
}
.register-btn:active {
  background-color: var(--blue-3);
}

@media screen and (max-width: 680px) {
  .register-form-ul {
    grid-template-columns: 1fr;
  }
}
@media screen and (max-width: 480px) {
  .register-form {
    width: 100%;
  }

  .register-form-ul input {
    min-width: 100%;
  }
}
